/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - begin
   ========================================================================== */
@include laptop-landscape-large {

    .woocommerce-page .qodef-content,
    div.woocommerce {

        .qodef-quantity-buttons {
            .qodef-quantity-input {
                top: -3px;
            }
        }
    }
}

@include phone-landscape {

    .woocommerce-page .qodef-content,
    div.woocommerce {

        .qodef-quantity-buttons {
            .qodef-quantity-input {
                padding: 0 5px;
            }

            .qodef-quantity-minus {
                left: 2px;
            }
            .qodef-quantity-plus {
                right: 2px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs responsive styles - end
   ========================================================================== */