// layout mixins - start

@mixin qodefRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin qodefTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin qodefTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin qodefTypographyLayout($important: null) {
    color: inherit $important;
    font-family: inherit $important;
    font-size: inherit $important;
    font-weight: inherit $important;
    font-style: inherit $important;
    line-height: inherit $important;
    letter-spacing: inherit $important;
    text-transform: inherit $important;
}

// layout mixins - end

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        position: absolute;
        top: 0;
        left: 0;
        display: inline-block;
        line-height: 0;
        pointer-events: none;
        cursor: pointer;
    }

    input[type=checkbox] + label span.qodef-label-text {
        display: inline-block;
        padding-left: 10px;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .qodef-label-view {
        display: inline-block;
        vertical-align: top;
        width: $checkbox-size;
        height: $checkbox-size;
        background-color: $default-background-color;
        border: $checkbox-border-width solid $default-border-color;
        border-radius: 2px;
        cursor: pointer;
        box-sizing: border-box;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .qodef-label-view:after {
        content: '';
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        background-color: $first-main-color;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }

    input[type=checkbox]:checked + label .qodef-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

