// common mixins - start

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
    @include qodefRelativeHolderLayout(top);
    margin: 17px 0 0;
    clear: both;

    ul {
        @include qodefRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 6px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                color: $default-text-color;
                font-weight: 400;

                &:after {
                    content: '';
                    height: 2px;
                    width: 11px;
                    position: absolute;
                    left: 50%;
                    bottom: 0;
                    background-color: $default-heading-color;
                    opacity: 0;
                    transform: translateX(-50%);
                }
            }

            &.qodef-pag-active {
                
                a {
                    color: $default-heading-color;

                    &:after {
                        opacity: 1;
                    }
                }
            }

            &.qodef-pag-prev,
            &.qodef-pag-next,
            &.qodef-pag-first,
            &.qodef-pag-last {
                margin: 0 2px;

                a {
                    font-size: 22px;
                    color: $default-heading-color;

                    span {
                        display: inline-block;
                        line-height: inherit;

                        &.qodef-nav-label {
                            display: inline-block;
                            font-weight: 600;
                            font-size: 15px;
                            position: relative;
                            bottom: 4px;
                        }

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            &.qodef-pag-next,
            &.qodef-pag-last {
                float: right;
            }

            &.qodef-pag-prev,
            &.qodef-pag-first {
                float: left;
            }

            @if ($list_type == 'shortcode') {
                
                &.qodef-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.qodef-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $default-heading-color;
                    font-weight: 400;

                    &:after {
                        content: '';
                        height: 2px;
                        width: 100%;
                        min-width: 11px;
                        background-color: $default-heading-color;
                        left: 50%;
                        bottom: 1px;
                        position: absolute;
                        transform: translateX(-50%);
                    }
                }

                a {

                    &:hover {
                        color: $default-heading-color;
                    }
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 22px;
                            line-height: 18px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            color: $default-heading-color;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin qodefButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 15px;
    line-height: 1.47em;
    letter-spacing: 0;
    font-weight: 600;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
    text-transform: lowercase;
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 15px 40px;

    } @else if ($size == 'medium') {
        padding: 17px 75px 16px;

    } @else if ($size == 'large') {
        padding: 19px 85px;

    } @else if ($size == 'huge') {
        display: block;
        width: 100%;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

