// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 $input-margin;
    padding: $input-vertical-padding 0;
    font-family: inherit;
    font-size: 15px;
    font-weight: 400;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    color: $default-text-color;
    background-color: transparent;
    border-radius: 0;
    outline: 0;
    cursor: text;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;
    border: none;
    border-bottom: 1px solid $default-heading-color;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

@mixin qodefUnderline($color: currentColor, $speed: .6s) {
    width: auto;
    background-repeat: no-repeat;
    background-image: linear-gradient(transparent calc(100% - 1px), $color 1px);
    background-image: -ms-linear-gradient(transparent 95%,currentColor 1px);
    transition: $speed cubic-bezier(0.215, 0.61, 0.355, 1);
    background-size: 0 100%;

    &.qodef-hovered,
    &:hover {
        background-size: 100% 100%;

        @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
            background-size: 100% 75%;
        }
    }
}

// mixins styles - end

