// animation mixin - start

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

@mixin qodefBody() {
    font-family: $default-heading-font;
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
}

@mixin qodefBlockquote($important : null) {
    @include qodefRelativeHolderLayout();
    margin: 10px 0;
    padding: 0;
    font-size: 18px;
    font-style: italic;
    line-height: 30px;
    box-sizing: border-box;
    color: #454545;
    quotes: "“" "”" "‘" "’";
    text-align: left;

    > * {
        @include qodefTypographyLayout();
        margin: 0;
    }

    p {
        display: inline;
    }

    &:before {
        content: open-quote;
    }

    &:after {
        content: close-quote;
    }

    cite {
        font-style: normal;
        font-weight: 400;
    }

    cite,
    .wp-block-quote__citation,
    .wp-block-pullquote__citation,
    footer {
        @include qodefTypographyLayout();
        font-style: normal;
        font-weight: 400;
        margin: 0;
    }

    .editor-rich-text__tinymce {
        line-height: inherit;
    }

}

// heading mixins - start

@mixin qodefDefaultHeadingStyle() {
    @include qodefHeadingStyle();
    margin: 25px 0;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    
    a {
        @include qodefTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin qodefHeadingStyle($with-heading: null, $with-color: true) {
    
    @if ($with-color) {
        color: $default-heading-color;
    }
    
    @if ($with-heading == 'h1') {
        @include qodefH1();
    } @else if ($with-heading == 'h2') {
        @include qodefH2();
    } @else if ($with-heading == 'h3') {
        @include qodefH3();
    } @else if ($with-heading == 'h4') {
        @include qodefH4();
    } @else if ($with-heading == 'h5') {
        @include qodefH5();
    } @else if ($with-heading == 'h6') {
        @include qodefH6();
    }
}

@mixin qodefH1() {
    font-size: 55px;
    line-height: 1.09em;
    font-weight: 300;
}

@mixin qodefH2() {
    font-size: 40px;
    line-height: 1.125em;
    font-weight: 300;
}

@mixin qodefH3() {
    font-size: 30px;
    line-height: 1.17em;
    font-weight: 400;
}

@mixin qodefH4() {
    font-size: 20px;
    line-height: 1.2em;
    font-weight: 600;
}

@mixin qodefH5() {
    font-size: 16px;
    line-height: 1.4em;
    font-weight: 600;
}

@mixin qodefH6() {
    font-size: 13px;
    line-height: 1.455em;
    font-weight: 600;
}

// heading mixins - end

